<script>
export default {
  name: 'HelloWorld',
  data() {
    return {
      show1: false,
      show2: false,
      show3: false,
      show4: false,
      show5: false,
      show6: false,
      show7: false,
      show8: false,
      showPickerView: true,
      tabbar: 0,
      counter: 0,
      campus: '请选择校区',
      centeredText: '场地分类',  // span标签中的文本
      description: '故障描述',
      remark: '备注',
      repairItem: '报修物品',
      value: '',
      input1: '',
      input2: '',
      input3: '',
      input4: '',
      input5: '',
      input6: '',
      imageUrl: '',
      attributeValue: 0,  // 初始值为0
      columns: ['二号楼', '六号楼', '三号楼',],
      columns1: ['一楼', '二楼', '三楼', '四楼', '五楼'], // 示例楼层选项
      columns2: ['101', '102', '103'], // 示例门牌号选项
      selectedBuilding: "请选择楼栋", // 初始化为默认值
      selectedFloor: "请选择楼层", // 初始化为默认值
      selectedHouseNumber: "请选择门牌号", // 初始化为默认值
      fileList: [], // 确保在data中定义了fileList属性
    };
  },
  computed: {
    formattedColumns() {
      return this.buildingOptions.map(option => ({ text: option, value: option }));
    },
  },
  methods: {
    reTrun() {
      if (this.active++ > 0) this.active = 0;
      this.showPickerView = true;
      this.site = '';
      this.article = '';
    },
    updateCounter() {
      if (this.counter < 8) {
        this.counter++;
      }
    },
    showPopup() {
      this.show1 = true;
    },
    showPopup1() {
      this.show2 = true;
    },
    showPopup2() {
      this.show3 = true;
    },
    showPopup3() {
      this.show4 = true;
    },
    showPopup4() {
      this.show5 = true;
    },
    showPopup5() {
      this.show6 = true;
    },
    showPopup6() {
      this.show7 = true;
    },
    showPopup7() {
      this.show8 = true;
    },
    handleValueChange(newValue) {
      // Map the selected value to the corresponding campus name
      switch (newValue) {
        case '1':
          this.campus = '科教城校区';
          break;
        case '2':
          this.campus = '沙太校区';
          break;
        case '3':
          this.campus = '荔湾校区';
          break;
        default:
          this.campus = ''; // Set a default value if needed
      }
    },
    handleAvatarSuccess(res, file) {
      this.imageUrl = URL.createObjectURL(file.raw);
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === 'image/jpeg';
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error('上传头像图片只能是 JPG 格式!');
      }
      if (!isLt2M) {
        this.$message.error('上传头像图片大小不能超过 2MB!');
      }
      return isJPG && isLt2M;
    },
    handleExceed() {
      this.$toast.warning('超出最大个数限制')
    },
    onPickerConfirm(value) {
      if (Array.isArray(value)) {
        // 如果 value 是数组，直接取第一个元素
        const selectedText = value[0];
        this.selectedBuilding = selectedText + " ";
      } else if (typeof value === 'string') {
        // 如果 value 是字符串，直接使用
        this.selectedBuilding = value + " ";
        this.show3 = false;
      } else {
        // 处理其他情况
        console.error('onPickerConfirm: value has unexpected type', value);
      }
    },
    onCancel() {
      this.show3 = false;
    },
    onCancel1() {
      this.show4 = false;
    },
    onCancel2() {
      this.show5 = false;
    },
    handleFloorSelection(value) {
      if (Array.isArray(value)) {
        const selectedText = value.join('');
        this.selectedFloor = selectedText + " ";
      } else if (typeof value === 'string') {
        // 处理仅选择一个值的情况
        this.selectedFloor = value + " ";
        this.show4 = false;
      } else {
        console.error('handleFloorSelection: value has unexpected type', value);
      }
    },
    handleHouseNumberSelection(value) {
      if (Array.isArray(value)) {
        const selectedText = value.join('');
        this.selectedHouseNumber = selectedText + " ";
      } else if (typeof value === 'string') {
        // 处理仅选择一个值的情况
        this.selectedHouseNumber = value + " ";
        this.show5 = false;
      } else {
        console.error('handleHouseNumberSelection: value has unexpected type', value);
      }
    },
    setAttribute() {
      this.attributeValue = 1;  // 当点击按钮时，设置属性值为1
      if (this.attributeValue === 1) {
        this.centeredText = '已填写';
        this.show2 = false;
      }
    },
    setAttributes() {
      this.attributeValue1 = 1;  // 当点击按钮时，设置属性值为1
      if (this.attributeValue1 === 1) {
        this.description = '已填写';
        this.show7 = false;
      }
    },
    setAttributesr() {
      this.attributeValue2 = 1;  // 当点击按钮时，设置属性值为1
      if (this.attributeValue2 === 1) {
        this.repairItem = '已填写';
        this.show6 = false;
      }
    },
    submit() {
      if (this.description === '故障描述') {
        alert('请填写完整信息');  // 如果description等于'故障描述'，显示一个提醒的弹窗
      } else {
        alert('你已完成申报');  // 否则，显示一个'你已完成申报'的弹窗
      }
    },
  },
  watch: {
    value: 'handleValueChange',
  },
  mounted() {
    // 在组件挂载时调用updateCounter()来更新内容
    this.updateCounter();
  },
}

</script>

